"use client"

import { useState, useEffect } from "react"
import { 
  Check, 
  X, 
  Eye, 
  FileText, 
  BadgeCheck, 
  AlertCircle,
  Search,
  ExternalLink
} from "lucide-react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"

// Data Simulasi Pendaftar UMKM
const MOCK_APPLICANTS = [
  {
    id: "UMKM-001",
    owner: "Siti Aminah",
    businessName: "Gudeg Yu Djum Cabang Jakarta",
    email: "aminah@gudeg.com",
    nik: "3275012345670001",
    npwp: "01.234.567.8-091.000",
    status: "pending",
    date: "2023-12-23"
  },
  {
    id: "UMKM-002",
    owner: "Bambang Pamungkas",
    businessName: "Sate Maranggi Pak Bambang",
    email: "bambang@sate.com",
    nik: "3275087654320002",
    npwp: "02.888.111.9-082.000",
    status: "pending",
    date: "2023-12-24"
  }
]

export default function UmkmVerificationPage() {
  const [applicants, setApplicants] = useState(MOCK_APPLICANTS)
  const [filter, setFilter] = useState("")

  const handleAction = (id: string, newStatus: "approved" | "rejected") => {
    const updated = applicants.map(app => 
      app.id === id ? { ...app, status: newStatus } : app
    )
    setApplicants(updated)
    const actionText = newStatus === "approved" ? "Disetujui" : "Ditolak"
    alert(`Mitra ${id} telah ${actionText}`)
  }

  const filteredApplicants = applicants.filter(app => 
    app.businessName.toLowerCase().includes(filter.toLowerCase()) ||
    app.owner.toLowerCase().includes(filter.toLowerCase())
  )

  return (
    <div className="space-y-6 font-sans">
      {/* Header Info */}
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 bg-white p-6 rounded-2xl border border-gray-200 shadow-sm">
        <div>
          <h2 className="text-2xl font-bold text-gray-800">Verifikasi Dokumen Mitra</h2>
          <p className="text-gray-500 text-sm">Tinjau KTP dan NPWP calon mitra sebelum memberikan akses Dashboard UMKM.</p>
        </div>
        <div className="flex gap-2">
          <div className="bg-yellow-50 text-yellow-700 px-4 py-2 rounded-xl border border-yellow-100 flex items-center gap-2">
            <AlertCircle size={18} />
            <span className="font-bold">{applicants.filter(a => a.status === 'pending').length} Menunggu</span>
          </div>
        </div>
      </div>

      {/* Search Bar */}
      <div className="relative">
        <Search className="absolute left-4 top-3.5 text-gray-400 w-5 h-5" />
        <input 
          type="text" 
          placeholder="Cari nama usaha atau pemilik..."
          className="w-full pl-12 pr-4 py-3.5 bg-white border border-gray-200 rounded-2xl focus:ring-2 focus:ring-[#a64029] focus:outline-none shadow-sm"
          value={filter}
          onChange={(e) => setFilter(e.target.value)}
        />
      </div>

      {/* Table List */}
      <div className="bg-white rounded-3xl border border-gray-200 shadow-sm overflow-hidden">
        <table className="w-full text-left border-collapse">
          <thead>
            <tr className="bg-gray-50 text-gray-400 text-xs uppercase tracking-widest font-bold">
              <th className="px-6 py-4">Informasi Bisnis</th>
              <th className="px-6 py-4">Data Legalitas (KTP/NPWP)</th>
              <th className="px-6 py-4">Tgl Daftar</th>
              <th className="px-6 py-4">Status</th>
              <th className="px-6 py-4 text-right">Tindakan</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-100 text-sm">
            {filteredApplicants.map((app) => (
              <tr key={app.id} className="hover:bg-gray-50/50 transition-colors">
                <td className="px-6 py-5">
                  <p className="font-bold text-gray-800">{app.businessName}</p>
                  <p className="text-gray-500 text-xs">{app.owner} â€¢ {app.email}</p>
                </td>
                <td className="px-6 py-5">
                  <div className="space-y-1">
                    <div className="flex items-center gap-2 text-gray-700 font-mono text-xs">
                      <BadgeCheck size={14} className="text-blue-500" /> {app.nik}
                    </div>
                    <div className="flex items-center gap-2 text-gray-700 font-mono text-xs">
                      <FileText size={14} className="text-gray-400" /> {app.npwp}
                    </div>
                  </div>
                </td>
                <td className="px-6 py-5 text-gray-500 italic">
                  {new Date(app.date).toLocaleDateString('id-ID', { day: 'numeric', month: 'short', year: 'numeric' })}
                </td>
                <td className="px-6 py-5">
                  {app.status === 'pending' && <Badge className="bg-yellow-100 text-yellow-700 border-none">Pending</Badge>}
                  {app.status === 'approved' && <Badge className="bg-green-100 text-green-700 border-none font-bold">Terverifikasi</Badge>}
                  {app.status === 'rejected' && <Badge className="bg-red-100 text-red-700 border-none">Ditolak</Badge>}
                </td>
                <td className="px-6 py-5">
                  <div className="flex justify-end gap-2">
                    {app.status === 'pending' ? (
                      <>
                        <button 
                          onClick={() => handleAction(app.id, 'approved')}
                          className="p-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-all shadow-sm"
                          title="Setujui Mitra"
                        >
                          <Check size={18} />
                        </button>
                        <button 
                          onClick={() => handleAction(app.id, 'rejected')}
                          className="p-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-all shadow-sm"
                          title="Tolak Mitra"
                        >
                          <X size={18} />
                        </button>
                      </>
                    ) : (
                      <button className="text-gray-400 hover:text-gray-600 p-2">
                        <ExternalLink size={18} />
                      </button>
                    )}
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        
        {filteredApplicants.length === 0 && (
          <div className="py-20 text-center">
            <div className="bg-gray-100 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
              <Search className="text-gray-300" />
            </div>
            <p className="text-gray-500 font-medium">Tidak ada pendaftar UMKM yang ditemukan.</p>
          </div>
        )}
      </div>
    </div>
  )
}